.bottomNavigator {
  height: 65px;
  background-color: #220646;
  position: absolute;
  left: 0;
  right: 0;
  bottom: 0;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}
.bottomTabBar {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  color: white;
  margin-inline: 3%;
  cursor: pointer;
}
